h1 {
  text-align: center;
  color: white;
  background-color: #3f51b5;
  margin: auto;
  padding: 15px;
  border-radius: 5px;
}

.container-input {
  text-align: center;
  margin-top: 30px;
}

.rate-container {
  margin: 50px 0 0 50px;
  float: left;
  background-color: #3f51b5;
  padding: 15px;
  border-radius: 5px;
  color: white;
}

.rate-header {
  font-size: 24px;
  padding-bottom: 10px;
}

.rate-value {
  margin-top: 7px;
}

.loading {
  font-size: 24px;
  text-align: center;
  margin-top: 200px;
  color: #3f51b5;
}

.error-message {
  margin-top: 20px;
  text-align: center;
  color: #d42f19;
  font-size: 20px;
}
